	module.exports = {"data":{"markdownRemark":{"html":"<h2>Converting Pounds to Ounces</h2>\n<p>One pound (lb) is equal to 16 ounces (oz). To convert pounds to ounces, you multiply the number of pounds by <code>16</code>. </p>\n<h3>Examples</h3>\n<p>Something that weighs <code>6 lbs</code> weighs <code>96 oz</code>, because <code>16 X 6</code> is <code>96</code>.</p>\n<p><code>.5 lbs</code> converts to <code>8 oz</code> because <code>.5 X 16 = 8</code>.</p>","fields":{"slug":"/mathematics/converting-pounds-to-ounces/"},"frontmatter":{"title":"Converting Pounds to Ounces"}}},"pathContext":{"slug":"/mathematics/converting-pounds-to-ounces/"}}

